
import React from 'react'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'

const Mywallets = props => {
    const {imgurl} = useSelector(state => state.data)
    return (
        <div className="">
                <Link to={`/coin/${props.item.id}`}>
                <div className="border-b-[1px] py-2 px-3 flex items-center justify-between dark:text-slate-200 dark:border-b-slate-500">
                    <div className="flex items-center gap-3">
                        <div className=""> {props.item.adminwallet.image && <img src={`${imgurl}/wallets/${props.item.adminwallet.image}`} alt="" className="w-8 rounded-full" />} </div>
                        <div className="">
                            <div className="capitalize font-semibold text-lg">{props.item.adminwallet.name}</div>
                            <div className="text-xs font-semibold text-slate-600 dark:text-slate-200">${parseInt(props.item.crypto / props.item.adminwallet.current_price).toFixed(2)} <span className="uppercase">usd</span> </div>
                        </div>
                    </div>
                    <div className="">
                        <div className="text-right font-semibold">{props.item.crypto / props.item.adminwallet.current_price !== 0 ? parseFloat(props.item.crypto).toFixed(9) : '0'} <span className="uppercase">{props.item.adminwallet.short}</span> </div>
                        <div className="text-right font-semibold text-green-600 text-sm">+{props.item.adminwallet.percent}%</div>
                    </div>
                </div>
            </Link>
        </div>
    )
}

export default Mywallets


